.paper {
    padding: 10px;
}

form {
    font-size: 14px;
    font-family: 'Open Sans';
}

.form__group {
    margin: 10px 0;
    width: 700px;
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-gap: 5px;
}

.form__group input {
    border: none;
    outline: none;
    color: #333;
    font-family: 'Open Sans';
}

.form__group input:focus {
    outline: none;
}

#execution__status {
    color: #992323;
}

.form__group label {
    font-weight: 600;
    background-color: rgb(247, 241, 241);
    /* background-color: lavender; */
    color: #1296ee;
    padding: 5px;
    border-radius: 5px;
}

.button {
    width: 100px;
    height: 20px;
    align-self: center;
    cursor: pointer;
    color: rgb(255, 255, 255);
    background-color: rgba(68, 65, 225, 0.8);
    font-family: inherit;
    border-radius: 3px;
    border: none;
    transition: all ease-in-out 0.2s;
}

.button:hover {
    background-color: rgba(68, 65, 225, 0.9);
}

.button:focus {
    outline: none;
}
